.categories__link {
  position: relative;
  display: block;
  padding: 27px 33px 32px 29px;
  color: #ffffff;
  font-size: 19px;
  font-weight: 700;
  line-height: 22px;
  background-color: #63d1bb;
}
.categories__link::after {
  transition-property: transform, opacity, color, background-color, border-color, box-shadow;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  transition-delay: 0;
  position: absolute;
  top: 50%;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  transform: translateY(-50%);
  opacity: 0.7;
  content: "";
}
.categories__link--dark {
  background-color: #56c3b2;
}
.categories__link--interior::after {
  width: 40px;
  height: 62px;
  right: 35px;
  background-image: url("../img/icon-interior.svg");
}
.categories__link--toys::after {
  width: 60px;
  height: 60px;
  right: 27px;
  background: url("../img/icon-toy.svg");
}
.categories__link:hover::after,
.categories__link:focus::after {
  opacity: 0.9;
}
.categories__link:active {
  color: rgba(255, 255, 255, 0.3);
}
.categories__link:active::after {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .categories {
    display: flex;
  }
  .categories__link {
    flex-basis: 50%;
    padding-left: 36px;
    font-size: 20px;
    line-height: 30px;
  }
  .categories__link--toys::after {
    right: 34px;
  }
}
