  padding: 0;
  margin: 0;
  list-style-type: none;
  transition-property: transform, opacity, color, background-color, border-color, box-shadow;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  transition-delay: 0;
  opacity: 1;
  transform: translateY(0);
}
