}
.modal--open {
  background-color: rgba(255, 255, 255, 0.9);
  pointer-events: auto;
}
.modal--open .modal__window {
  opacity: 1;
  animation: modal-show 0.76s;
}
.modal__window {
  transition-property: transform, opacity, color, background-color, border-color, box-shadow;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  transition-delay: 0.25s;
  width: 100%;
  max-width: 568px;
  padding: 30px 30px 45px 30px;
  text-align: center;
  background-color: #ffffff;
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.25);
  opacity: 0;
}
.modal__title {
  font-size: 25px;
  line-height: 30px;
  text-transform: uppercase;
}
.modal__descriptor {
  margin-top: 13px;
  margin-bottom: 0;
  line-height: 30px;
}
.modal__form {
  max-width: 350px;
  margin-top: 23px;
  margin-right: auto;
  margin-left: auto;
}
.modal__button {
  margin-top: 31px;
}
@media (min-width: 768px) {
