.form__legend {
  display: flex;
  align-items: center;
  margin-bottom: 21px;
  color: #63d1bb;
  font-size: 17px;
  font-weight: 700;
  text-transform: uppercase;
}
.form__legend--nomargin {
  margin-bottom: 0;
}
.form__legend--name {
  margin-bottom: 24px;
}
.form__line {
  flex-basis: 0;
  flex-grow: 1;
  height: 1px;
  margin-left: 20px;
  background-color: #63d1bb;
}
.form__choose {
  display: block;
  margin-top: 18px;
}
.form__choose:first-of-type {
  margin-top: 0;
}
.form__label {
  font-size: 17px;
  font-weight: 700;
  line-height: 18px;
}
.form__handle {
  position: relative;
  display: block;
  min-height: 40px;
  padding-left: 60px;
  padding-top: 8px;
}
.form__handle::before,
.form__handle::after {
  transition-property: transform, opacity, color, background-color, border-color, box-shadow;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  transition-delay: 0;
  position: absolute;
  content: "";
}
.form__handle::before {
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  border: 2px solid #000000;
}
.form__handle::after {
  opacity: 0;
}
.form__handle--radio::before,
.form__handle--radio::after {
  border-radius: 50%;
}
.form__handle--radio::after {
  top: 12px;
  left: 12px;
  width: 16px;
  height: 16px;
  background-color: #63d1bb;
  transform: scale(0.25, 0.25);
}
.form__handle--checkbox::after {
  top: 12px;
  left: 9px;
  width: 22px;
  height: 16px;
  background: url("../img/icon-tick.svg") 50% 50%;
  background-size: 22px 16px;
  transform: scale(0.8, 0.8);
}
.form__handle:hover::before,
.form__handle:focus::before {
  border-color: #63d1bb;
}
.form__handle:active::before {
  border-color: rgba(0, 0, 0, 0.3);
}
.form__hidden:checked + .form__handle::after {
  opacity: 1;
  transform: scale(1, 1);
}
.form__group-wrapper {
  margin-top: 26px;
}
.form__group-wrapper:first-of-type {
  margin-top: 0;
}
.form__group--margin-top {
  margin-top: 27px;
}
.form__textgroup {
  position: relative;
  display: flex;
  flex-direction: column;
}
.form__textgroup:first-child {
  margin-top: 0;
}
.form__textarea-group,
.form__textgroup {
  margin-top: 22px;
}
.form__icon {
  transition-property: transform, opacity, color, background-color, border-color, box-shadow;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  transition-delay: svg;
  transition-property: opacity, fill;
  position: absolute;
  right: 3px;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0.2;
  pointer-events: none;
}
.form__icon--phone {
  right: 7px;
}
.form__input {
  padding-right: 40px;
}
.form__input:hover + .form__icon,
.form__input:focus + .form__icon,
.form__input:active + .form__icon {
  opacity: 1;
}
.form__input:hover + .form__icon,
.form__input:focus + .form__icon {
  fill: #63d1bb;
}
.form__textarea {
  height: 142px;
}
.form__notice {
  margin-top: 15px;
  margin-bottom: 0;
  color: #444444;
  font-size: 14px;
  line-height: 16px;
}
.form__button {
  margin-top: 32px;
}
.form__sizes {
  display: flex;
  justify-content: space-between;
  margin-right: -3.5px;
  margin-left: -3.5px;
}
.form__size-wrapper {
  flex-basis: 33.33%;
  padding: 0 3.5px;
}
.form__size {
  transition-property: transform, opacity, color, background-color, border-color, box-shadow;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  transition-delay: 0;
  display: block;
  padding: 16px;
  font-size: 17px;
  line-height: 16px;
  text-transform: uppercase;
  font-weight: 700;
  border: 2px solid rgba(68, 68, 68, 0.1);
  cursor: pointer;
}
.form__size:hover,
.form__size:focus {
  border-color: rgba(68, 68, 68, 0.4);
}
.form__hidden:checked + .form__size {
  border-color: #63d1bb;
}
.form__column--checkboxes .form__choose {
  margin-top: 21px;
}
.form__column--checkboxes .form__choose:first-child {
  margin-top: 0;
}
@media (min-width: 768px) {
