  }
  .form__choose {
    margin-top: 25px;
    cursor: pointer;
  }
  .form__label {
    font-size: 19px;
  }
  .form__label--margin-right {
    margin-right: 25px;
  }
  .form__textgroup {
    flex-direction: row;
    align-items: center;
    margin-top: 23px;
  }
  .form__textarea-group {
    margin-top: 0;
  }
  .form__control {
    flex-direction: row;
