    margin-top: 0;
    font-size: 17px;
    line-height: 30px;
  }
  .form__button {
    margin: 0;
  }
  .form__icon {
    right: 22px;
  }
  .form__icon--phone {
    right: 25px;
  }
}
@media (min-width: 1150px) {
  .form__column {
    flex-basis: 0;
    flex-grow: 1;
  }
