    flex-basis: 115px;
    /* - IE */
    flex-grow: 0;
    margin-left: 60px;
  }
  .form__column--flex .form__choose {
    flex-basis: 263px;
  }
  .form__column--flex .form__choose--right {
    margin-left: 0;
  }
  .form__column--padding-right {
