  }
  .form__control {
    padding-right: 225px;
    padding-left: 225px;
  }
  .form__label--margin-right {
    margin-right: 27px;
  }
  .form__group--last {
    padding-bottom: 46px;
  }
  .form__textgroup {
    margin-top: 24px;
  }
  .form__icon {
    right: 4px;
  }
  .form__icon--phone {
    right: 8px;
  }
